.page5 {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 30px 0;
  padding-left: 80px;
}
.slider1 {
  height: 100%;
  width: 100%;
  /* background-color: black; */
  overflow: hidden;
}

.slider1 > .swiper {
  width: 130vw !important;
  height: 100%;
  cursor: pointer;
  position: relative;
}

.slider1 > .swiper-slide {
  position: relative;
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.slider1 .swiper-slide::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #c1552764;
  top: 0;
  left: 0;
  opacity: 0;
  transition: all linear 0.3s;
}

.slider1 .swiper-slide:hover::before {
  opacity: 1;
}

.slider1 > .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
